/**
 * Simple client-hydrated counter.
 */

export function getServerSideProps() {
  return { initial: 1 };
}

export default function Counter(props: { initial: number }) {
  let count = props.initial;
  return (
    <main style={{ fontFamily: "sans-serif", padding: 24 }}>
      <h1>Counter</h1>
      <p>Initial from server: {props.initial}</p>
      <button onClick={(e) => {
        count++;
        (e.currentTarget.nextSibling as HTMLElement).textContent = String(count);
      }}>+1</button>
      <span style={{ marginLeft: 12 }}>{count}</span>
    </main>
  );
}


